import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import "assets/css/App.css";
import {
  FormControl,
  FormHelperText,
  InputLabel,
  Select,
  MenuItem,
  Grid,
} from "@material-ui/core";

export default function FormDialog({ handleSnackbar }) {
  const [bassinsList, setBassinsList] = useState("");
  const [open, setOpen] = useState(false);
  const [bassin, setBassin] = useState("");
  const [date, setDate] = useState("");

  useEffect(() => {
    var url = "/api/bassins";
    authFetch(url)
      .then((response) => {
        if (response.status === 401) {
          handleSnackbar("danger", "Sorry you aren't authorized!");
          return null;
        } else if (response.status === 500) {
          handleSnackbar("warning", "Bad request");
          return null;
        }
        return response.json();
      })
      .then((response) => {
        setBassinsList(response.bassins);
        setBassin(response.bassins[0]);
      });
  }, [handleSnackbar]);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setBassin("");
    setDate("");
  };

  const handleAddClose = () => {
    setOpen(false);
    var url = "/api/cycles";
    const requestOptions = {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ Bassin: bassin, Date: date }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.status === 401) {
          return null;
        } else if (response.status === 500) {
          return null;
        }
        return response.json();
      })
      .then((response) => {
        response !== null
          ? handleSnackbar("success", response.message)
          : handleSnackbar("warning", "Bad request");
      });
  };

  const handleBassinChange = (e) => {
    setBassin(e.target.value);
  };

  const handleDateChange = (e) => {
    setDate(e.target.value);
  };

  return (
    <div>
      <Button
        id="ButtonDialog"
        variant="contained"
        color="secondary"
        onClick={handleClickOpen}
      >
        Add Cycle
      </Button>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Ajouter un cycle</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Veuillez choisir le Bassin et la date de remplissage.
          </DialogContentText>
          <Grid container spacing={1}>
            <Grid item>
              <FormControl>
                <InputLabel id="bassin-label">Bassin</InputLabel>
                <Select
                  labelId="bassin-label"
                  id="bassin-select"
                  value={bassinsList[0]}
                  onChange={handleBassinChange}
                >
                  {bassinsList !== ""
                    ? bassinsList.map((Bassin) => (
                        <MenuItem value={Bassin}>{Bassin}</MenuItem>
                      ))
                    : null}
                </Select>
                <FormHelperText>Sélectionner un bassin</FormHelperText>
              </FormControl>
            </Grid>
            <Grid item>
              <TextField
                required
                id="data_rempli"
                label="Date Remplissage"
                type="date"
                defaultValue={new Date().toISOString().substring(0, 10)}
                variant="filled"
                onChange={handleDateChange}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Annuler
          </Button>
          <Button onClick={handleAddClose} color="primary">
            Ajouter
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
