import React, { useState, useEffect } from "react";
import ChartistGraph from "react-chartist";
import { authFetch } from "auth";
import { makeStyles } from "@material-ui/core/styles";
import Snackbar from "components/Snackbar/Snackbar.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import Button from "@material-ui/core/Button";
import "assets/css/App.css";
import {
  FormControl,
  FormHelperText,
  InputLabel,
  Select,
  MenuItem,
  Grid,
} from "@material-ui/core";
// icons
import {
  AccessTime,
  ArrowUpward,
  ArrowDownward,
  ArrowForward,
} from "@material-ui/icons";
// styles
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
// ##############################
// // // variables used to create animation on charts
// #############################
var delays = 80,
  durations = 500;

var Chartist = require("chartist");

const useStyles = makeStyles(styles);

export default function AnalysesEau() {
  const [bassinsList, setBassinsList] = useState(["B12"]);
  const [bassin, setBassin] = useState("B12");
  const [tc, setTC] = useState(false);
  const [color, setColor] = useState("primary");
  const [message, setMessage] = useState("");
  const [dates, setDates] = useState(["10/06/1998"]);
  const [temps, setTemps] = useState(["102"]);
  const [O2, setO2] = useState(["102"]);
  const [ph, setPh] = useState(["102"]);
  const [SE, setSE] = useState(["102"]);

  const classes = useStyles();

  const optionsTemp = {
    lineSmooth: Chartist.Interpolation.cardinal({
      tension: 1,
    }),
    low: 0,
    high: 50, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
    chartPadding: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
  };

  const optionsO2 = {
    lineSmooth: Chartist.Interpolation.cardinal({
      tension: 1,
    }),
    low: 0,
    high: 10, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
    chartPadding: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
  };

  const optionsPH = {
    lineSmooth: Chartist.Interpolation.cardinal({
      tension: 1,
    }),
    low: 0,
    high: 14, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
    chartPadding: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
  };

  const optionsSE = {
    lineSmooth: Chartist.Interpolation.cardinal({
      tension: 1,
    }),
    low: 0,
    high: 70, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
    chartPadding: {
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
    },
  };

  const animation = {
    draw: function (data) {
      if (data.type === "line" || data.type === "area") {
        data.element.animate({
          d: {
            begin: 600,
            dur: 700,
            from: data.path
              .clone()
              .scale(1, 0)
              .translate(0, data.chartRect.height())
              .stringify(),
            to: data.path.clone().stringify(),
            easing: Chartist.Svg.Easing.easeOutQuint,
          },
        });
      } else if (data.type === "point") {
        data.element.animate({
          opacity: {
            begin: (data.index + 1) * delays,
            dur: durations,
            from: 0,
            to: 1,
            easing: "ease",
          },
        });
      }
    },
  };

  useEffect(() => {
    var url = "/api/bassins";
    authFetch(url)
      .then((response) => {
        if (response.status === 401) {
          handleSnackbar("danger", "Sorry you aren't authorized!");
          return null;
        } else if (response.status === 500) {
          handleSnackbar("warning", "Bad request");
          return null;
        }
        return response.json();
      })
      .then((response) => {
        setBassinsList(response.bassins);
        setBassin(response.bassins[10]);
        updateBassin(response.bassins[10]);
      });
  }, []);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  const updateBassin = (bassin) => {
    var url = "/api/analyseEauGraph/" + bassin;
    authFetch(url)
      .then((response) => {
        if (response.status === 401) {
          handleSnackbar("danger", "Sorry you aren't authorized!");
          return null;
        }
        return response.json();
      })
      .then((response) => {
        setDates(response.dates);
        setTemps(response.temps);
        setO2(response.oxygene);
        setPh(response.ph);
        setSE(response.se);
      });
  };

  const handleBassinChange = (e) => {
    setBassin(e.target.value);
    updateBassin(e.target.value);
  };

  return (
    <div id="#copeyito">
      <Grid container spacing={1}>
        <Snackbar
          place="tc"
          color={color}
          message={message}
          open={tc}
          closeNotification={() => setTC(false)}
          close
        />
        <Grid item xs={6} align="center">
          <Card chart>
            <CardHeader color="success">
              <ChartistGraph
                className="ct-chart"
                data={{ labels: dates, series: [temps] }}
                type="Line"
                options={optionsTemp}
                listener={animation}
              />
            </CardHeader>
            <CardBody>
              <h4 className={classes.cardTitle}>Température de l'eau</h4>
              <p className={classes.cardCategory}>
                {TextEvolution(temps, "Temperature")}
              </p>
            </CardBody>
            <CardFooter chart>
              <div className={classes.stats}>
                <AccessTime /> updated 4 minutes ago
              </div>
            </CardFooter>
          </Card>
        </Grid>
        <Grid item xs={6} align="center">
          <Card chart>
            <CardHeader color="warning">
              <ChartistGraph
                className="ct-chart"
                data={{ labels: dates, series: [O2] }}
                type="Line"
                options={optionsO2}
                listener={animation}
              />
            </CardHeader>
            <CardBody>
              <h4 className={classes.cardTitle}>Oxygène</h4>
              <p className={classes.cardCategory}>{TextEvolution(O2, "O2")}</p>
            </CardBody>
            <CardFooter chart>
              <div className={classes.stats}>
                <AccessTime /> updated 4 minutes ago
              </div>
            </CardFooter>
          </Card>
        </Grid>
        <Grid item xs={6} align="center">
          <Card chart>
            <CardHeader color="info">
              <ChartistGraph
                className="ct-chart"
                data={{ labels: dates, series: [ph] }}
                type="Line"
                options={optionsPH}
                listener={animation}
              />
            </CardHeader>
            <CardBody>
              <h4 className={classes.cardTitle}>PH de l'eau</h4>
              <p className={classes.cardCategory}>{TextEvolution(ph, "PH")}</p>
            </CardBody>
            <CardFooter chart>
              <div className={classes.stats}>
                <AccessTime /> updated 4 minutes ago
              </div>
            </CardFooter>
          </Card>
        </Grid>
        <Grid item xs={6} align="center">
          <Card chart>
            <CardHeader color="danger">
              <ChartistGraph
                className="ct-chart"
                data={{ labels: dates, series: [SE] }}
                type="Line"
                options={optionsSE}
                listener={animation}
              />
            </CardHeader>
            <CardBody>
              <h4 className={classes.cardTitle}>SE de l'eau</h4>
              <p className={classes.cardCategory}>{TextEvolution(SE, "SE")}</p>
            </CardBody>
            <CardFooter chart>
              <div className={classes.stats}>
                <AccessTime /> updated 4 minutes ago
              </div>
            </CardFooter>
          </Card>
        </Grid>
        <Grid item xs={12} spacing={1} align="center" container>
          <Grid item xs={6} align="right">
            <FormControl>
              <InputLabel id="bassin-label">Bassin</InputLabel>
              <Select
                labelId="bassin-label"
                id="bassin-select"
                value={bassin}
                onChange={handleBassinChange}
              >
                {bassinsList !== ""
                  ? bassinsList.map((Bassin) => (
                      <MenuItem value={Bassin}>{Bassin}</MenuItem>
                    ))
                  : null}
              </Select>
              <FormHelperText>Sélectionner un bassin</FormHelperText>
            </FormControl>
          </Grid>
          <Grid item xs={6} align="left" justify="center">
            <Button
              color="primary"
              variant="contained"
              onClick={() => updateBassin(bassin)}
            >
              Update
            </Button>
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
}

const computeEvolution = (list) => {
  return (
    ((parseFloat(list[list.length - 1]) - parseFloat(list[list.length - 2])) /
      parseFloat(list[list.length - 2])) *
    100
  );
};

const TextEvolution = (list, name) => {
  const classes = useStyles();
  const evol = computeEvolution(list);
  if (isNaN(evol)) {
    return null;
  }
  if (evol < 0) {
    return (
      <span className={classes.dangerText}>
        <ArrowDownward className={classes.upArrowCardCategory} />
        {evol}% decrease in today {name}.
      </span>
    );
  } else if (evol === 0) {
    return (
      <span className={classes.successText}>
        <ArrowForward className={classes.upArrowCardCategory} />
        0% evolution in today {name}.
      </span>
    );
  } else {
    return (
      <span className={classes.successText}>
        <ArrowUpward className={classes.upArrowCardCategory} />
        {evol}% increase in today {name}.
      </span>
    );
  }
};
