import React, { useState, useEffect } from "react";
import "assets/css/App.css";
import { authFetch } from "auth";
import CustomTableEdit from "components/CyclesTable";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import { Grid, CardHeader } from "@material-ui/core";
import FormDialog from "components/AddCycleDialog";
import Snackbar from "components/Snackbar/Snackbar.js";

export default function Cycles() {
  const [tableHeaders, setTableHeaders] = useState("");
  const [tableData, setTableData] = useState("");
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  useEffect(() => {
    var url = "/api/cycles";
    authFetch(url)
      .then((response) => {
        if (response.status === 401) {
          handleSnackbar("danger", "Sorry you aren't authorized!");
          return null;
        }
        return response.json();
      })
      .then((response) => {
        setTableHeaders(Object.keys(response.cycles[0]));
        setTableData(response.cycles);
      });
  }, [tc]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  return (
    <div id="#copeyito">
      <Grid container spacing={1}>
        <Snackbar
          place="tc"
          color={color}
          message={message}
          open={tc}
          closeNotification={() => setTC(false)}
          close
        />
        <Grid item xs={12} align="center">
          {tableHeaders === "" || tableData === "" ? null : (
            <Card>
              <CardHeader
                id="Header"
                color="primary"
                title="Cycles"
                action={
                  <FormDialog
                    handleSnackbar={(cl, ms) => handleSnackbar(cl, ms)}
                  />
                }
              />
              <CardBody>
                <CustomTableEdit
                  tableHeaderColor="primary"
                  tableHead={tableHeaders}
                  setMessage
                  tableData={tableData}
                  postUrl="/api/cycles"
                  newLine={false}
                  handleSnackbar={handleSnackbar}
                />
                <br />
              </CardBody>
            </Card>
          )}
        </Grid>
      </Grid>
    </div>
  );
}
