import React, { useState, useEffect } from "react";
import { Formik, Field, Form, useField } from "formik";
import {
  TextField,
  Button,
  Grid,
  Typography,
  FormControl,
  InputLabel,
  Select,
  FormHelperText,
  MenuItem,
  CardHeader,
} from "@material-ui/core";
import { authFetch } from "auth";
import CustomTableEdit from "components/TableEdit";
import Card from "components/Card/Card.js";
import MyCardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import Snackbar from "components/Snackbar/Snackbar.js";

export default function Peches() {
  const [bassinsList, setBassinsList] = useState([]);
  const [bassin, setBassin] = useState("");
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");
  const [tableHeaders, setTableHeaders] = useState("");
  const [tableData, setTableData] = useState("");

  const setTable = (resp) => {
    setTableData(resp.data);
    setTableHeaders(resp.headers);
  };

  useEffect(() => {
    var url = bassin !== "" ? "/api/analyseEau/" + bassin : "/api/bassins";
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        bassin === "" ? setBassinsList(response.bassins) : setTable(response);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [bassin]);

  useEffect(() => {
    if (bassinsList.length > 0 && bassin === "") {
      setBassin(bassinsList[0]);
    }
  }, [bassinsList]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };
  return (
    <div>
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Card>
        <Grid item align="center">
          <CardHeader
            id="Header"
            title="Analyse d'eau"
            color="black"
            action={
              <div
                style={{
                  background: "#f50057",
                  borderRadius: "20px",
                  padding: "1em",
                  marginRight: "3em",
                }}
              >
                <FormControl>
                  <Select
                    style={{ marginTop: 0, color: "white" }}
                    labelId="bassin-label"
                    id="bassin-select"
                    value={bassin}
                    onChange={(e) => setBassin(e.target.value)}
                  >
                    {bassinsList !== ""
                      ? bassinsList.map((Bassin) => (
                          <MenuItem value={Bassin}>{Bassin}</MenuItem>
                        ))
                      : null}
                  </Select>
                  <FormHelperText style={{ color: "white" }}>
                    Sélectionner un bassin
                  </FormHelperText>
                </FormControl>
              </div>
            }
          />
        </Grid>
        <CardBody>
          <Grid container spacing={1}>
            <Grid item xs={8} align="center">
              {tableHeaders === "" || tableData === "" ? null : (
                <Card>
                  <MyCardHeader color="info">
                    <Typography variant="h4">
                      Dernières données pour le bassin {bassin}
                    </Typography>
                  </MyCardHeader>
                  <CardBody>
                    <CustomTableEdit
                      tableHeaderColor="info"
                      tableHead={tableHeaders}
                      tableData={tableData}
                      postUrl="/api/table/AnalyseEau"
                    />
                  </CardBody>
                </Card>
              )}
            </Grid>
            <Grid item xs={4} align="center">
              <Formik
                initialValues={{
                  date: new Date().toISOString().substring(0, 10),
                  O2: null,
                  PH: null,
                  Temp: null,
                  SE: null,
                }}
                onSubmit={(data, { setSubmitting }) => {
                  setSubmitting(true);
                  var url = "/api/analyseEau";
                  const requestOptions = {
                    method: "PUT",
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify({ ...data, bassin: bassin }),
                  };
                  authFetch(url, requestOptions)
                    .then((response) => {
                      if (response.ok) {
                        return response.json();
                      } else {
                        throw new Error(response.statusText);
                      }
                    })
                    .then((response) =>
                      handleSnackbar("success", response.message)
                    )
                    .catch((e) =>
                      handleSnackbar("danger", e.name + ": " + e.message)
                    );
                  setSubmitting(false);
                }}
              >
                {({ values, isSubmitting }) => (
                  <Form>
                    <Grid
                      style={{ height: "80vh" }}
                      container
                      spacing={4}
                      direction="column"
                      alignItems="center"
                      justify="center"
                    >
                      <Grid item align="center">
                        <Typography variant="h4">
                          Rentrez les données:
                        </Typography>
                      </Grid>
                      <Grid item align="center">
                        <Field
                          name="date"
                          label="Date"
                          type="date"
                          variant="outlined"
                          as={TextField}
                        />
                      </Grid>
                      <Grid item align="center">
                        <Field
                          name="O2"
                          label="O2"
                          type="number"
                          variant="outlined"
                          as={TextField}
                        />{" "}
                        <Field
                          name="PH"
                          label="PH"
                          type="number"
                          variant="outlined"
                          as={TextField}
                        />
                      </Grid>
                      <Grid item align="center">
                        <Field
                          name="Temp"
                          label="Temperature"
                          type="number"
                          variant="outlined"
                          as={TextField}
                        />{" "}
                        <Field
                          name="SE"
                          label="SE"
                          type="number"
                          variant="outlined"
                          as={TextField}
                        />
                      </Grid>
                      <Grid item align="center">
                        <Button
                          disabled={isSubmitting}
                          type="submit"
                          variant="contained"
                          color="secondary"
                        >
                          Submit
                        </Button>
                      </Grid>
                    </Grid>
                  </Form>
                )}
              </Formik>{" "}
            </Grid>
          </Grid>
        </CardBody>
      </Card>
    </div>
  );
}
